export default {
	data() {
		return {

		};
	},
	props: {
		goodsinfo: {
			type: String,
			default: {}
		},
		type: {
			type: [String, Number],
			default: 0
		}
	},
	filters: {
		filtertag(value) {
			if (value == 1) {
				return '自营'
			} else if (value == 2) {
				return '品牌'
			} else {
				return '基地'
			}
		}
	}
}
